import {
	SELECT_RACE,
	SELECT_NAME,
	SET_POPULATION_SIZE,
	POST_MESSAGE,
	NEW_PLAYERS,
	ADD_SOCKET,
	LOGIN,
	LOGOUT,
	SHOW_STATISTICS,
	JOKE,
	IRONY
} from '../constants'

const initState = {
	race: 'red',
	messages: {},
	message: "let's start",
	isLogin: false,
	statistics: [0,0,0]
}

const phrases = [
	"–ø—Ä–µ–¥–∞—Ç–µ–ª—å!!!", 
	"–¥–∞,–¥–∞ –±—Ä–æ—Å—å –Ω–∞—Å –≤ —Å–∞–º—ã–π –≤–∞–∂–Ω—ã–π –º–æ–º–µ–Ω—Ç...", 
	"—Ç—Ä—É—Å–ª–∏–≤–∞—è –±—É–∫–∞—à–∫–∞!!!",
	"–û—Ç –º–µ–Ω—è –∏ —Ç–æ '–ë–û–õ–¨–®–ï' —Ç–æ–ª–∫—É",
	"–û–¥–Ω–æ–∫–ª–µ—Ç–æ—á–Ω–æ–µ!!!"
	]
const phrase = [
	"–°–µ—Ä—å–µ–∑–Ω–æ?! —Ç—ã –Ω–µ –º–æ–∂–µ—à—å –∑–∞–∫—Ä—ã—Ç—å –æ–∫–Ω–æ? –ê —Ö–æ—á–µ—à—å –ø–æ–±–µ–¥–∏—Ç—å –≤ '–±–µ—Å–∫–æ–Ω–µ—á–Ω–æ–π –≤–æ–π–Ω–µ'?=)))",
	"–ù—É –∏ –≥–ª—É–ø–æ–≤–∞—Ç —Ç—ã...=)",
	"–ú–æ–ª–æ–¥–µ—Ü!!! —Ç—ã —ç—Ç–æ —Å–¥–µ–ª–∞–ª!!!"
]

const rootReducer = (state = initState, action) => {
	switch(action.type){
		case SELECT_NAME:
			return {
				...state,
				name: action.payload
			}
		case JOKE:
			return {
				...state,
				message: phrases[Math.floor(Math.random() * phrases.length)]
			}
		case SHOW_STATISTICS:
			return {
				...state,
				statistics: action.payload
			}
		case LOGIN:
			return{
				...state,
				isLogin: true
			}
		case IRONY:{
			return {
				...state,
				message: !state.isLogin ? phrase[Math.floor(Math.random() * phrase.length)]: state.message
			}
		}
		case LOGOUT:
			return{
				...state,
				isLogin: false
			}
		case SELECT_RACE:
			return {
				...state,
				race: action.payload
			}
		case NEW_PLAYERS:
			return {
				...state,
				players: action.payload
			}
		case SET_POPULATION_SIZE:
			return {
				...state,
				population: action.payload
			}
		case POST_MESSAGE:
			return {
				...state,
				message: action.payload
			}
		case ADD_SOCKET:
			return {
				...state,
				socket: action.payload
			}
		
		case 'SHOW_MESSAGE':
			const {race, message} = action.payload
			const messages = { ...state.messages } 
			messages[race] = message
			return{
				...state,
				messages: messages
			}
		default:
			return state
	}
}


export {
	rootReducer
}